import React, { useState, useEffect } from 'react'
import {Routes, Route} from 'react-router-dom'
// import Faq from './routes/Faq';
import Home from './components/home';
// import Pricing from './routes/Pricing'
// import Contact from './routes/Contact'

import { useWeb3React } from "@web3-react/core";
import { BrowserRouter, Switch, Redirect } from "react-router-dom";

import { useEagerConnect, useInactiveListener } from "./hooks";
import getWeb3 from "./getWeb3";

function App() {
  const [activatingConnector, setActivatingConnector] = useState();
  const { connector, active, chainId, account } = useWeb3React();


  const triedEager = useEagerConnect();

  useInactiveListener(!triedEager || !!activatingConnector);

  useEffect(() => {
    if (activatingConnector && activatingConnector === connector) {
      setActivatingConnector(undefined);
    }
  }, [activatingConnector, connector]);

  useEffect(() => {
    if (active) localStorage.setItem("shouldEggerConnect", "true");
  }, [active]);

  useInactiveListener(!triedEager || !!activatingConnector);

  useEffect(() => {
    if (activatingConnector && activatingConnector === connector) {
      setActivatingConnector(undefined);
    }
  }, [activatingConnector, connector]);

  useEffect(() => {
    if (active) localStorage.setItem("shouldEggerConnect", "true");
  }, [active]);

  useEffect(() => {
    const assignWeb3 = async () => {
      window.web3 = await getWeb3();
    };
    if (account) {
      assignWeb3();
    }
  }, [account]);



  const onChangeNetworkClick = async () => {
    // Metamask adds Ropsten chain by default, so no need to check wether chain is added or not
    const connectedWallet = localStorage.getItem("connectedWallet");
    
      try {
        await window.ethereum.request({
          method: "wallet_switchEthereumChain",
          params: [{ chainId: "0x38" }],
        });
        window.location.reload();
      } catch (error) {
        if (error.code === 4902) {
          try {
            await window.ethereum.request({
              method: "wallet_addEthereumChain",
              params: [
                {
                  rpcUrls: ["https://bsc-dataseed.binance.org/"],
                  chainId: "0x38",
                  nativeCurrency: {
                    name: "Binance",
                    symbol: "BNB",
                    decimals: 18,
                  },
                  blockExplorerUrls: ["https://bscscan.com"],
                  chainName: "Bsc Mainnet",
                },
              ],
            });
            window.location.reload();
          } catch (addError) {
            console.error(addError);
          }
          console.error(error);
        } else {
          // if no window.ethereum then MetaMask is not installed
          alert(
            "MetaMask is not installed. Please consider installing it: https://metamask.io/download.html"
          );
        }
      }
    
  };

  // useEffect(() => {
  //   if (chainId !== parseInt(56) && active) {
  //     alert("Please switch to Binance Chain Mainet")
  //     onChangeNetworkClick();
  //   }
  // })

  return (
    <>
    <Routes>
      <Route path='/' element={<Home />}/>
      {/* <Route path='/pricing' element={<Pricing />} />
      <Route path='/faq' element={<Faq />} />
      <Route path='/contact' element={<Contact />} /> */}
    </Routes>
    </>
  );
}

export default App;
